

export interface User {
    username: string,
    favourites: string[],
}



export interface AuthType {
    user: User | null;
    isAuthenticated: boolean;

}

export interface Restaurant {
    title: string,
    rating: number,
    place: string,
    category: Category,
}

export enum Category {
    All = "All Restaurants",
    FastFood = "Fast Food",
    FineDining = "Fine Dining",
    Healthy = "Healthy",
    Desserts = "Desserts",
}